*{
  margin: 0;padding: 0;box-sizing: border-box;
}
.main_div{
  width: 100%;
  height: 100vh;
  background: #2ecc71;
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: center;
}
.center_div{
  width: 25%;
  height: 80vh;
  background-color: #f4f4f4;
  box-shadow: 5px 5px 25px -5px rgb(0,0,0,0.5);
  border-radius: 15px;
}
h1{
  color: white;
  background: transparent;
  background-color: #8566aa;
  padding: 6px 0 2px 0;
  margin-bottom: 10px;
  box-shadow: 5px 5px 15px -5px rgb(0,0,0,0.3);
}
input{
  text-align: center;
  height: 30px;
  top: 10px;
  border: none;
  background: transparent;
  font-size: 20px;
  font-weight: 500;
  width: 60%;
  border-bottom: 2px solid #8566aa;
  outline: none;
  margin-right: 20px;
}

 .new_btn{ 
  width: 50px;
  height: 55px;
 background-color: #16a085 !important;
 color: white !important;
  border-radius: 50% !important;
 
  box-shadow: 5px 5px 15px -5px rgb(0,0,0,0.3);
}
.new_btn:hover{
  background-color:green !important;
  
}
.MuiSvgIcon-root{
width: 2rem !important;
height: 2rem !important;
}

ol{
  margin-top: 30px;
}
ol li{
  padding-left: 0px;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  min-height: 40px;
  display: flex;
  align-items: center;
  color:#8566aa;
  text-transform: capitalize;
  list-style: none;
}
.todo_style{
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;

}
.todo_style .deletIcon{
  width: 20px;
  height: 20px;
  display: flex;
   justify-content: center;
   align-items: center;
   background-color: #8566aa;
   border-radius: 50%;
   margin: 0 15px 0 35px;
   color: aliceblue;
   box-shadow: 5px 5px 15px -5px rgb(0,0,0,0.3);
   padding: 5px;
}
.todo_style.deleteIcon:hover{
 
  background-color: crimson;
 
}
.todo_style.fa-times:hover.list_style{
  color: crimson !important;
}
@media(max-width:768px){
  .center_div{
    width: 55%;
}
.todo_style .fa-times{
  margin: 0 15px 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
} 
button{
margin-left: 5px;
min-height: 40px;
width: 40px;
color: white;
font-size: 30px;
}
}
